<template>
  <div>
    <mu-appbar style="width: 100%;" :color="$store.state.color">
      <div slot="left" color="#3051f1">
        <a @click="()=>{
            $router.go(-1)}">
          <mu-button icon size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">污染溯源</span>
      </div>
      <div slot="right" class="icon" size="20" @click="openBotttomSheet()">
        <mu-button icon slot="left" class="icon" size="20">
          <img src="static/img/queryNews.png" alt srcset />
        </mu-button>
      </div>
    </mu-appbar>
    <div class="contentTable">
      <div class="flexColumn headerTable">
        <div>上报时间</div>
        <div>水站名称</div>
        
        <div>上报人</div>
        <div>数据详情</div>
      </div>
      <div
        v-for="(item,index) in itemList"
        :key="index"
        class="flexColumn"
        @click="ListDetails(item.id)"
      >
        <div v-if="itemList.length!==0">{{item.gmtCreate}}</div>
        <div>{{item.station}}</div>
        <!-- <div>{{item.enterprise}}</div> -->
        <div>{{item.person}}</div>
        <div :style="'color:'+$store.state.color">详情</div>
      </div>
    </div>
    <van-pagination
      v-model="currentPage"
      :total-items="24"
      :items-per-page="5"
      @change="changePage"
    />
    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <mu-list-item>
            <div class="titleselect">
              <span>站点名称：</span>
            </div>
            <div class="select" @click="selectDataPic" id="selectStation">
              <!-- <select name v-model="normal.value1">
                <option  :value="item" v-for="(item,index) in options" :key="index">{{item}}</option>
              </select>-->
              <input type="text" v-model="normal.value1" placeholder="请选择站点" class="input" disabled />
              <img src="static/img/toBottom.png" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="titleselect">
              <span>开始时间：</span>
            </div>
            <div class="select" @click="selectStartTime('start')">
              <input type="text" class="input" disabled v-model="value3" placeholder="选择开始时间" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="titleselect">
              <span>结束时间：</span>
            </div>
            <div class="select" @click="selectStartTime('end')">
              <input type="text" class="input" disabled v-model="value4" placeholder="选择结束时间" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="btnBottom">
              <div class="left" @click="closeBottomSheet()">取消</div>
              <div class="right" :style="'background:'+$store.state.color" @click="confirmData()">确定</div>
            </div>
          </mu-list-item>
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
    <mu-container>
      <mu-bottom-sheet :open.sync="openTime">
        <mu-list @item-click="closeBottomSheet">
          <mu-sub-header>时间筛选</mu-sub-header>
          <van-datetime-picker
            v-model="currentDate"
            type="date"
            :min-date="minDate"
            @confirm="confirmDate"
            @cancel="closeTime"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
    <div class="positionAbAdd">
      <AddPollution />
    </div>
  </div>
</template>
<script>
// const h = document.documentElement.clientHeight || document.body.clientHeight;
// let heightDevice = Math.floor((h - 56-50) / 50);
import AppBace from "../../../static/ajax";
import AddPollution from "@/components/AddPollution/index";
export default {
  data() {
    return {
      open: false,
      itemList: [1],
      currentPage: 1,
      token: "",
      arrList: [],
      value3: "",
      value4: "",
      openTime: false,
      minDate: new Date("1970-07-01"),
      currentDate: new Date(),
      showTime: "",
      normal: {
        value1: ""
      }
    };
  },
  methods: {
    confirmData() {
      this.initDataList();
      this.open = false;
    },
    openBotttomSheet() {
      this.open = true;
    },
    selectStartTime(e) {
      this.openTime = true;
      this.showTime = e;
    },
    confirmDate(value) {
      this.openTime = false;
      switch (this.showTime) {
        case "start":
          this.value3 = AppBace.filter(value);
          break;

        case "end":
          this.value4 = AppBace.filter(value);
          break;
      }
    },
    closeTime() {
      this.openTime = false;
    },
    ListDetails(id) {
      console.log(id);
      var array = this.arrList;
      for (let index = 0; index < array.length; index++) {
        var dataId = array[index].id;
        if (id === dataId) {
          this.$store.commit("pollutionDataListID", array[index]);
        }
      }
      this.$router.push("PollutionTracingDetails");
    },
    async initDataList() {
      var url = "/pollutionTrace";
      var _this = this;
      var res = await AppBace.request(
        url,
        "GET",
        {
          startTime: _this.value3,
          endTime: _this.value4,
          station: _this.normal.value1,
          pageNum: _this.currentPage,
          pageSize: 10
        },
        this.token
      );
      //   console.log(res);
      this.arrList = res.data.data.rows;
      this.itemList = this.formenuDataList(res.data.data.rows);
    },
    formenuDataList(res) {
      var arr = [];
      arr = res;
      for (let index = 0; index < arr.length; index++) {
        var time = arr[index].gmtCreate;

        arr[index].gmtCreate = time.slice(0, 10);
      }
      return arr;
    },
    changePage(value) {
      //   console.log(value);
      this.currentPage = value;
      this.initDataList();
    },
    selectDataPic() {
      this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: selectStation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.menu, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择站点", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.normal.value1 = selectArr[0];
          _this.open = true;
          // var name = Echarts.switchData(selectArr[0]);
          // _this.initData(name);
          // console.log(JSON.stringify(selectArr))
        }
      });
    },
    closeBottomSheet() {
      this.open = false;
      this.value3 = "";
      this.value4 = "";
      this.normal.value1 = "";
      this.initDataList();
    }
  },
  created() {
    this.token = sessionStorage.getItem("token");
    this.initDataList();
  },
  components: {
    AddPollution
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
.icon img {
  width: 26px;
  height: 26px;
}
.flexColumn {
  display: flex;
  flex-direction: row;
  height: 50px;
  align-items: center;
  border-bottom: 1px solid rgb(235, 235, 235);
}
.flexColumn div {
  height: 40px;
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contentTable {
  background-color: white;
}
.headerTable {
  border-bottom: 1px solid #ccc;
  background-color: #d4e2fe;
  font-weight: 600;
}
.positionAbAdd{
  position: absolute;
  top: 20%;
  right: 20px;
  box-shadow: 5px 5px 5px 2px #ccc;
  width: 26px;
  height: 26px;
  border-radius: 13px;
}
</style>


